.Submit {
    margin-top: 10%;
    border: none;
    background: transparent;
    position: relative;
    display: inline-block;
    width: 25.6em;
    height: 6.3em;
    overflow: hidden;
    vertical-align: middle;
    text-align: left;
    text-decoration: none;
    line-height: 3em;
    font-size: 1rem;
    color: #fff;
    cursor: pointer;
    transform: translateZ(0);
    transition: transform 0.5s;
  }
.Submit svg {
    z-index: 1;
    position: absolute;
    top: 0;
    left: 0;
  }
.Submit svg g line,
.Submit svg g polyline,
.Submit svg g circle {
    fill: none;
    stroke: #fff;
  }
.Submit svg g line,
.Submit svg g polyline {
    stroke-miterlimit: 10;
  }
.Submit svg g circle {
    display: block;
    stroke-linecap: round;
    stroke-linejoin: round;
    stroke-width: 0.066em;
    stroke-dasharray: 200;
    stroke-dashoffset: 0;
    transition: stroke-dashoffset ease-out 0.5s;
  }
.Submit font {
    z-index: 2;
    position: relative;
    display: inline-block;
    text-transform: uppercase;
    font-family: "Roboto", Helvetica, Arial, sans-serif;
    font-size: 1.5em;
  }
.Submit:hover {
    border: none;
    background: transparent;
    transform: translateZ(0) translateX(0.5em);
  }
.Submit:hover svg g circle {
    stroke-dashoffset: 200;
  }
  
.but-text {
    font-size: 300%; 
    font-family: 'Wire One', sans-serif;
}





